const galExp = function (a, n) {
  if (n === 0) {
    return 1;
  }
  if (a === 0) {
    return 0;
  }

  const logA = logTable[a];
  let logResult = Math.floor(logA * n);
  while (logResult >= 255) {
    logResult -= 255;
  }
  return expTable[logResult];
};

function galDivide(a, b) {
  if (a == 0) {
    return 0;
  }
  if (b == 0) {
    throw new Error("Argument 'divisor' is 0");
  }

  const logA = Number(logTable[a]);
  const logB = Number(logTable[b]);
  let logResult = logA - logB;
  if (logResult < 0) {
    logResult += 255;
  }
  return expTable[logResult];
}

function galMultiply(a, b) {
  return mulTable[a][b];
}

function galMulSlice(c, ins, out) {
  out = out.slice(0, ins.length);
  if (c === 1) {
    return ins;
  }
  const mt = mulTable[c].slice(0, 256);
  for (let n = 0; n < ins.length; n++) {
    out[n] = mt[ins[n]];
  }

  return out;
}

const galMulSliceXor = (c, inArray, outArray, o) => {
  let out = outArray.slice(0, inArray.length);

  if (c === 1) {
    sliceXor(inArray, out, o);
    return out;
  }

  const mt = mulTable[c].slice(0, 256);

  for (let n = 0; n < inArray.length; n++) {
    out[n] ^= mt[inArray[n]];
  }

  return out;
};

const sliceXor = (inArray, outArray, o) => {
  return sliceXorGo(inArray, outArray, o);
};

const sliceXorGo = (inArray, outArray) => {
  while (outArray.length >= 32) {
    const inS = inArray.slice(0, 32);
    const v0 = uint8ArrayToUint64(outArray.slice(0, 8)) ^ uint8ArrayToUint64(inS.slice(0, 8));
    const v1 = uint8ArrayToUint64(outArray.slice(8, 16)) ^ uint8ArrayToUint64(inS.slice(8, 16));
    const v2 = uint8ArrayToUint64(outArray.slice(16, 24)) ^ uint8ArrayToUint64(inS.slice(16, 24));
    const v3 = uint8ArrayToUint64(outArray.slice(24, 32)) ^ uint8ArrayToUint64(inS.slice(24, 32));

    uint64ToUint8Array(outArray, 0, v0);
    uint64ToUint8Array(outArray, 8, v1);
    uint64ToUint8Array(outArray, 16, v2);
    uint64ToUint8Array(outArray, 24, v3);

    outArray = outArray.slice(32);
    inArray = inArray.slice(32);
  }

  outArray = outArray.slice(0, inArray.length);

  for (let n = 0; n < inArray.length; n++) {
    outArray[n] ^= inArray[n];
  }

  return outArray;
};

// 辅助函数：将 Uint8Array 转换为 Uint64
const uint8ArrayToUint64 = (array) => {
  return (
    (array[0] << 0) |
    (array[1] << 8) |
    (array[2] << 16) |
    (array[3] << 24) |
    (array[4] << 32) |
    (array[5] << 40) |
    (array[6] << 48) |
    (array[7] << 56)
  );
};

// 辅助函数：将 Uint64 转换为 Uint8Array
const uint64ToUint8Array = (array, offset, value) => {
  for (let i = 0; i < 8; i++) {
    array[offset + i] = (value >> (i * 8)) & 0xff;
  }
};

